<template>
  <div>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        Chinese Name 中文姓名
      </el-col>
      <el-col :lg="8">
        <el-input v-model="form.chineseName" placeholder="中文姓名" ref="chineseName"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        English Name 英文姓名
      </el-col>
      <el-col :lg="8">
        <el-input v-model="form.englishName" placeholder="英文姓名" ref="englishName"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        Parent's Name 家长姓名
      </el-col>
      <el-col :lg="8">
        <el-input v-model="form.parentName" placeholder="家长姓名" ref="parentName"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        Email 邮件
      </el-col>
      <el-col :lg="8">
        <el-input v-model="form.email" placeholder="邮件" ref="email"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        Gender 性别
      </el-col>
      <el-col :lg="8">
        <el-radio v-model="form.gender" label="男">男</el-radio>
        <el-radio v-model="form.gender" label="女">女</el-radio>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        Birthday 生日
      </el-col>
      <el-col :lg="8">
        <el-date-picker
          :editable="false"
          :clearable="false"
          v-model="form.birthday"
          type="date"
          placeholder="生日">
        </el-date-picker>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        Home Address 家庭住址
      </el-col>
      <el-col :lg="8">
        <el-input v-model="form.address" placeholder="家庭住址" ref="address"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        Current School 现就读学校
      </el-col>
      <el-col :lg="8">
        <el-input v-model="form.currentSchool" placeholder="现就读学校" ref="currentSchool"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        Grade Starting August 2018(2018年8月升读年级)
      </el-col>
      <el-col :lg="8">
        <el-input v-model="form.startGrade" placeholder="2018年8月升读年级" ref="startGrade"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        Mobile 手机号码
      </el-col>
      <el-col :lg="8">
        <el-input v-model="form.phone" placeholder="手机号码" type="number" ref="phone"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        Camp(s) to be taken 报读
      </el-col>
      <el-col :lg="8">
        <el-radio v-model="form.camp" label="A">A</el-radio>
        <el-radio v-model="form.camp" label="B">B</el-radio>
        <el-radio v-model="form.camp" label="A&B">A&B</el-radio>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        School Bus Service 校巴服务
      </el-col>
      <el-col :lg="8">
        <el-radio v-model="form.bus" label="yes">YES需要</el-radio>
        <el-radio v-model="form.bus" label="no">NO不需要</el-radio>
      </el-col>
    </el-row>

    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :lg="5">
        <el-button type="primary" @click="edit">Add</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {sendMail} from 'api/mail'
export default {
  name: 'HelloWorld',
  data () {
    return {
      form: {
        chineseName: '',
        englishName: '',
        parentName: '',
        email: '',
        gender: '男',
        birthday: new Date(),
        address: '',
        currentSchool: '',
        phone: '',
        startGrade: '',
        camp: 'A',
        bus: 'yes'
      },
      editable: true
    }
  },
  methods: {
    edit() {
      if (this.editable) {
        this.editable = false
        if (this.form.chineseName === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.$refs.chineseName.focus()
          this.editable = true
          return
        }
        if (this.form.englishName === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.$refs.englishName.focus()
          this.editable = true
          return
        }
        if (this.form.parentName === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.$refs.parentName.focus()
          this.editable = true
          return
        }
        if (this.form.email === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.$refs.email.focus()
          this.editable = true
          return
        }
        if (this.form.address === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.$refs.address.focus()
          this.editable = true
          return
        }
        if (this.form.currentSchool === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.$refs.currentSchool.focus()
          this.editable = true
          return
        }
        if (this.form.startGrade === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.$refs.startGrade.focus()
          this.editable = true
          return
        }
        if (this.form.phone === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.$refs.phone.focus()
          this.editable = true
          return
        }
        this.editable = true
        console.log(this.form)
        let body = ''
        body = '中文姓名:' + this.form.chineseName + '\n' +
          '英文姓名:' + this.form.englishName + '\n' +
          '家长姓名:' + this.form.parentName + '\n' +
          '邮件:' + this.form.email + '\n' +
          '性别:' + this.form.gender + '\n' +
          '生日:' + this.form.birthday.getFullYear() + '-' + (this.form.birthday.getMonth() + 1) + '-' + this.form.birthday.getDate() + '\n' +
          '家庭住址:' + this.form.address + '\n' +
          '现就读学校:' + this.form.currentSchool + '\n' +
          '2018年8月升读学校:' + this.form.startGrade + '\n' +
          '报读:' + this.form.camp + '\n' +
          '校巴服务:' + this.form.bus + '\n'
        sendMail('rainbowhuang@bcis.net.cn', 'Summer Camp ' + this.form.chineseName + '(' + this.form.englishName + ')', body)
        sendMail('lindacui@bcis.net.cn', 'Summer Camp ' + this.form.chineseName + '(' + this.form.englishName + ')', body)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
  @import "~variables"
  .row
    margin-bottom $medium-margin
</style>



